.header {
  position: relative;
  z-index: 1; }
  .header .overlay {
    opacity: 0;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(#2d314d, transparent); }
  .header.open .header__toggle > span:first-child {
    transform: rotate(40deg); }
  .header.open .header__toggle > span:nth-child(2) {
    opacity: 0; }
  .header.open .header__toggle > span:last-child {
    transform: rotate(-40deg); }
  .header nav {
    position: relative;
    background-color: white;
    padding-top: 1.0625rem;
    padding-bottom: 1.0625rem; }
  .header__logo img {
    width: 8rem;
    height: 1.375rem; }
  .header__toggle > span {
    display: block;
    width: 26px;
    height: 2px;
    background-color: #2d314d;
    transition: all 300ms ease-in-out;
    transform-origin: 2.5px 1px; }
    .header__toggle > span:not(:last-child) {
      margin-bottom: 5px; }
  .header__menu {
    background: white;
    margin-top: 1.5rem;
    padding: 1.625rem;
    border-radius: 5px;
    position: absolute;
    width: 100%;
    width: calc(100% - 3rem);
    left: 50%;
    transform: translateX(-50%); }
    .header__menu a {
      display: block;
      padding: 0.625rem;
      color: #2d314d;
      text-align: center; }
  .header__links a {
    position: relative;
    font-size: 0.875rem;
    color: #9698a6;
    transition: color 300ms ease-in-out; }
    .header__links a:not(:last-child) {
      margin-right: 32px; }
    .header__links a::after {
      content: '';
      display: block;
      position: absolute;
      height: 4px;
      left: 0;
      right: 0;
      bottom: -30px;
      background: linear-gradient(to right, #31d35c, #2bb7da);
      opacity: 0;
      transform-origin: center;
      transform: scale(0);
      transition: all 300ms ease-in-out; }
    .header__links a:hover {
      color: #2d314d; }
      .header__links a:hover::after {
        transform: scale(1);
        opacity: 1; }

